.form {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  top: 3rem;
  font-family: PT Serif;
  width: 45vw;
  margin: auto;
  padding: 2rem;
  border-radius: 0.2rem;
  background: linear-gradient(90deg, #d3d3f5, #ebedf1, #d3d3f5);

  .title {
    font-size: 1.1rem;
    width: 55vw;
    text-align: center;
    background: #ffffff;
    border-radius: 0.3rem;
    padding: 0.8rem;
    border: 0.1rem solid #7b7b88;
    text-transform: uppercase;
  }

  .name {
    text-align: center;
    display: block;
    margin: 0.2rem;
    font-weight: bold;
    font-size: 1rem;

    input,
    select {
      width: 55vw;
      padding: 0.5rem;
      margin-top: 0.3rem;
      border: 0.1rem solid #7b7b88;
      border-radius: 0.3rem;
      filter: drop-shadow(0 0.2rem 0.2rem #8888df);

      &:focus {
        outline: none;
        border-color: #007bff;
      }
    }

    span.error-message {
      color: red;
    }
  }

  .reservation {
    font-family: "PT Serif", serif;
    padding: 0.8rem 1.2rem;
    background-color: #9fa1a0;
    color: white;
    border: 0.1rem solid #9595aa;
    border-radius: 0.4rem;
    cursor: pointer;
    margin-top: 0.5rem;
    text-transform: uppercase;
    transition: background-color 0.15s ease;
    filter: drop-shadow(0 0.1rem 0.1rem #a5a4a4);
    box-shadow: 0 0 0.1rem #7f7f80;

    &:hover {
      background-color: #0c3768;
      border: 0.1rem solid #f4f4f8;
      filter: drop-shadow(0 0.1rem 0.2rem #f9f8fa);
    }

    &:disabled {
      background-color: #ccc;
      cursor: not-allowed;
    }
  }
}
