.info {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  font-family: PT Serif;

  .title {
    font-size: 1.4rem;
    padding: 1rem;
    text-transform: uppercase;
    background: radial-gradient(circle at center, #aad1ba, transparent);
  }

  .vision {
    display: flex;
    flex-direction: column;
    align-items: center;

    .subtitle {
      text-transform: uppercase;
    }
    .subtext {
    }
    .photo {
      height: 15rem;
    }
  }
}
