.history {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3rem;
  font-family: PT Serif;

  .title {
    font-size: 1.4rem;
    padding: 1rem;
    text-transform: uppercase;
    background: radial-gradient(circle at center, #aac8d1, transparent);
  }

  .container {
    display: flex;
    flex-direction: row;
    margin-top: 3rem;
    margin-bottom: 3rem;
    justify-content: space-between;

    .team {
      display: flex;
      flex-direction: column;
      height: 20rem;

      .subtitle {
      }

      .subtext {
        text-align: start;
      }
    }

    .photo {
      height: 20rem;
      filter: drop-shadow(0 0.1rem 0.1rem #333232);
    }
  }
}
