.table {
  font-family: "PT Serif";
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0.5rem;
  margin-top: 5rem;
  margin-bottom: 4rem;


  .title,
  .text {
    width: 25vw;
    text-align: center;
    border: 0.1rem solid #949597;
    border-radius: 0.3rem;
    filter: drop-shadow(0 0.1rem 0.1rem #ddddeb);
    background: linear-gradient(0deg, #d9dcdd, #dde4f3, #d9dcdd);

    &.day,
    &.hours {
      color: #08294e;
      background: #fafdfd;
      padding: 0.4rem;
      border-radius: 0.4rem;
    }
  }
}
