@import url("https://fonts.googleapis.com/css2?family=PT+Serif:wght@700&display=swap");

* {
  box-sizing: border-box;

  body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;

    header {
      background: lightgrey;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      box-shadow: 0rem 0.1rem 0.2rem #b1a9a9;
      filter: drop-shadow(0rem 0.1rem 0.1rem #646060);
      top: 0;
      left: 0;
      right: 0;

      .logo {
        height: 11rem;
        margin: -3rem 0vw -1rem 2vw;
      }

      .glasses {
        left: 0vw;
        top: 3.2rem;
        position: absolute;
        height: 4rem;
        transform: scaleX(-1);
      }
    }

    main {
    }

    footer {
      width: 100vw;
      font-family: PT Serif;
      filter: drop-shadow(0rem 0.1rem 0.15rem #5a5959);
      background: #ffffff;
    }
  }
}
