.data {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-around;
  padding-top: 0.5rem;

  .fb {
    position: relative;
    font-size: 2.5rem;
    border-radius: 0.3rem;
    background: none;
    text-align: center;
    width: 3rem;
    height: 3rem;
    color: #030353;
    transition: color 0.1s ease;
    border: none;
    cursor: pointer;
    filter: drop-shadow(0rem 0.1rem 0.1rem #636060);

    &:hover {
      color: #3379cf;
      box-shadow: 0rem 0.1rem 0.1rem #3c3c3d;
    }
  }

  .phone,
  .adress {
    border-radius: 0.3rem;
    text-align: center;
    text-decoration: none;
    font-size: 2.5rem;
    width: 3rem;
    height: 3rem;
    transition: color 0.1s ease;
    filter: drop-shadow(0rem 0.1rem 0.1rem #615e5e);
  }

  .phone {
    color: #030353;

    &:hover {
      color: #318b28;
      box-shadow: 0rem 0.1rem 0.1rem #3c3c3d;
    }
  }

  .adress {
    color: #030353;
    filter: drop-shadow(0rem 0.1rem 0.1rem #ebe5e5);

    &:hover {
      color: #eb0707;
      box-shadow: 0rem 0.1rem 0.1rem #3c3c3d;
    }
  }
}

.copyright {
  padding: 0.5rem;
  margin-block-end: 0;
  text-align: center;
}
