.brands {
  font-family: PT Serif;

  .title {
    text-transform: uppercase;
    text-align: center;
  }

  .text {
    margin: 2rem;
  }

  .subtitle {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    font-size: 1.8rem;
    text-align: center;
    background-color: gray;
    color: bisque;

    .model {
      width: 17vw;
    }
  }

  .container {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-left: 12vw;

    .brand {
      text-align: center;
      margin: 1rem;

      .logo {
        width: 28vw;
      }
    }
  }

  .functions {
    text-align: center;
    font-size: 1.5rem;
    text-transform: uppercase;
  }

  .list {
    .first {
      color: #e4a316;
    }

    .second {
      width: 70vw;
    }
  }

  .subtext {
    text-transform: uppercase;
    margin: 1rem 0 1rem 0;
    padding: 1rem 1vw 1rem 1vw;
    text-align: center;
    color: #131212;
    border-radius: 0.3rem;
    background-color: #ffffff;
    filter: drop-shadow(0 0.1rem 0.1rem #464444);
  }
}
