.welcome {
  margin-top: 4rem;
  font-family: PT Serif;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  box-shadow: 0 0.1rem 0.3rem #969797;

  .container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
    right: 0;
    width: 100vw;
    border-radius: 0.3rem;
    filter: drop-shadow(0 0 0.1rem #c7c3c3);
    background: linear-gradient(90deg, #d3d3f5, #ebedf1, #d3d3f5);

    .greeting,
    .intro,
    .instructions {
      text-align: end;
      padding-right: 6vw;
      padding-right: 3vw;
      margin: 1rem;
    }

    .greeting {
      font-size: 1.2rem;
      text-transform: uppercase;
    }

    .intro {
      font-size: 1rem;
    }

    .instructions {
      text-align: end;
      border-radius: 0.3rem;
      font-size: 1rem;
    }
  }

  .salon {
    position: relative;
    border-radius: 0.3rem;
    clip-path: polygon(3% 0, 100% 0, 100% 100%, 3% 100%);
    right: 0;
    width: 100vw;
    z-index: -1;  }
}
